            //
            add_show: false,
            AddFormData: {
                content: "",
                imgs: [],
                enable_check: 1
            },
            
            // 
            edit_show: false,
            EditFormData: {
                content: "",
                imgs:[],
                enable_check: ""
            },
            

            //
