            CommentFormData: {
                uuid: "",
                content: ""
            },

            // 
            detail_show: false,
            DetailFormData:{
                uuid:"",
                imgs:[]
            },
            pickerOptions:{ 
                disabledDate:(time) => { 
                    return this.dealDisabledDate(time) 
                } 
            } 

        });
    }

    export default {
        components: {
            file
        },
        data() {
            let that = this;
            // return store.state.WorkData;
            return {
                value1: '',
                value2: '',
                pickerOptionsstart:{
                    disabledDate(time) { //
                    const one = 30 * 24 * 3600 * 1000
                    // const minTime = this.choiceDate - one
                    // const maxTime = this.choiceDate + one
                    // return time.getTime() < minTime || time.getTime() > maxTime
						return time.getTime() > new Date(that.value2).getTime() ||  time.getTime() < (new Date(that.value2).getTime() - one);
					}
                },
                pickerOptionsend: {
                     disabledDate(time) { //
                    const one = 30 * 24 * 3600 * 1000
                    // const minTime = this.choiceDate - one
                    // const maxTime = this.choiceDate + one
                    // return time.getTime() < minTime || time.getTime() > maxTime
						return time.getTime() < new Date(that.value1).getTime() ||  time.getTime() > (new Date(that.value1).getTime() + one) ||  time.getTime() > (new Date().getTime());
					}
                },







                rules: {
                      content: [
                        { required: true, message: '', trigger: 'blur' },
                        ],
                        check_content: [
                            { required: true, message: '', trigger: 'blur' },
                        ],  
                        check_grade: [
                            { required: true, message: '', trigger: 'blur' },
                        ]
                },
            rows: [],
            total: 0,
            loading: false,

            curr_row: null,

            //
            search_show: false,
            SearchFormData: {
                start_end: ['', ''], 
                order_field: "add_time",
                order_sort: "asc",
                page_num: 1,
                page_len: 10
            },
            
            //
             enable_value: 0,
              enables_value: 1,
            enable_options: [
                {value: 1, label: ''},
                {value: 0, label: ''}
            ],
            add_show: false,
            AddFormData: {
                content: "",
                imgs: [],
                enable_check: 1
            },
            
            // 
            edit_show: false,
            EditFormData: {
                content: "",
                imgs:[],
                enable_check: ""
            },
            

            //
            check_show: false,
            CheckFormData: {
                uuid: "",
                imgs:[],
                check_content: "",
                check_grade: ""
            },
            

            // 
            comment_show: false,
            CommentFormData: {
                uuid: "",
                content: ""
            },

            // 
            detail_show: false,
            DetailFormData:{
                uuid:"",
                imgs:[]
            },
            pickerOptions:{ 
                disabledDate:(time) => { 
                    return this.dealDisabledDate(time) 
                } 
            },
            
            startTime : '',
            endTime: '',
            rangeTime: '',
            datePickerOptions:{
    disabledDate:(time)=>{
        let nowDate = new Date();
        let oneDay = 1000 * 60 * 60 * 24;
        let oneYearLater = new Date(nowDate.getTime() + (oneDay * 365));
        return time.getTime() < nowDate || time.getTime() > oneYearLater;
    }
},



            }
        },
        computed: {
            img_host: () => {
                return store.state.AppData.img_host;
            },
            width: () => {
                return store.state.AppData.width;
            },
            height: () => {
                return store.state.AppData.height;
            },
            upload_url: () => {
                return store.state.AppData.upload_url;
            },
        },
        created() {
            this.initTime()
            this.init();
        },
        methods: {
            initTime: function () {
                let oneDay = 1000 * 60 * 60 * 24;
                let dateNextZero = new Date(new Date().getTime() - oneDay*30);
                dateNextZero.setHours(0);
                dateNextZero.setMinutes(0);
                dateNextZero.setSeconds(0);
                // let date6Later = new Date(new Date().getTime() + (oneDay * 6));
                let date6Later = new Date(new Date().getTime());
                date6Later.setHours(23);
                date6Later.setMinutes(59);
                date6Later.setSeconds(59);

                this.startTime = this.formatDateTime(dateNextZero.getTime());
                this.endTime = this.formatDateTime(date6Later.getTime());
                this.rangeTime = [this.startTime, this.endTime];
            },
            formatDateTime(date) {
                var date = new Date(date);
                var YY = date.getFullYear() + '-';
                var MM = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
                var DD = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate());
                var hh = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
                var mm = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
                var ss = (date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds());
                return YY + MM + DD +" "+hh + mm + ss;
            },




            dealDisabledDate (time) {
            //  = 8.64e7  return
            let curDate = (new Date()).getTime();
            let day = 30 * 24 * 3600 * 1000;
            let dateRegion = curDate - day;
            return time.getTime() > Date.now() || time.getTime() < dateRegion;
          },

            handleSizeChange(val) {console.log(` ${val} `);},
            //  menu: local
            onSubMenu(menu, local = false) {
                util.submenu(menu, this, lime.cookie_get("login_token"), local);
            },

            
            // 
            init() {
                // this.loading = true;
                 NProgress.start();
                // let start_date = '';
                // let end_date = '';
                // console.log(this.rangeTime)
                // if (this.rangeTime && this.rangeTime[0] != '') {
                //     start_date = this.rangeTime[0].substring(0, 10);
                // }
                // if (this.rangeTime && this.rangeTime[1] != '') {
                //     end_date = this.rangeTime[1].substring(0, 10);
                // }

                 let start_date = this.value1 ? util.eleDate(this.value1) : '';
                let end_date = this.value2 ? util.eleDate(this.value2) : '';


                // if (this.SearchFormData.start_end[0] != '') {
                //     start_date = util.dateString(this.SearchFormData.start_end[0]);
                // }
                // let end_date = '';
                // if (this.SearchFormData.start_end[1] != '') {
                //     end_date = util.dateString(this.SearchFormData.start_end[1]);
                // }

                lime.req("ShopWorkDayList", {
                    login_token: lime.cookie_get("login_token"),

                    start_date: start_date,
                    end_date: end_date,

                    order_field: this.SearchFormData.order_field,
                    order_sort: this.SearchFormData.order_sort,
                    page_num: this.SearchFormData.page_num,
                    page_len: this.SearchFormData.page_len
                }).then(res => {
                    // this.loading = false;
                     NProgress.done()
                    this.rows = res.data.rows;
                    this.total = res.data.total;
                    this.curr_row = null;
                }).catch(err => {
                    this.$message.error(err)
                    // this.$router.push('/login');
                });
                // 
                setTimeout(() => {
                      NProgress.done()
                    //   this.$router.push('/login');
                    // this.loading = false;
                }, 10000);
            },

            // 
            onSortChange(sort) {
                this.SearchFormData.order_field = sort.prop;
                if (sort.order == "ascending") {
                    this.SearchFormData.order_sort = "asc";
                } else {
                    this.SearchFormData.order_sort = "desc";
                }

                this.init();
            },
            // 
            onSelectRow(row) {
                this.curr_row = row;
            },
            //
            onPageChange(page) {
                this.SearchFormData.page_num = page;
                this.init();
            },

            // 
            onRefresh() {
                this.init();
            },

            // 
            onSearch() {
                this.search_show = true;
            },
            onSearchSubmit() {
                this.init();
                this.search_show = false;
            },

            // 
            handleAdd() {
                this.add_show = true;
            },

                // 
            handleAddUploadSuccess(res, file, fileList) {
                if (res.code == 0) {
                    file.url = store.state.AppData.img_host + res.data.url;
                    
                    this.AddFormData.imgs = [];
                    fileList.forEach(item => {
                        this.AddFormData.imgs.push(item.url)
                    })
                } else {
                    this.$message.error(res.msg);
                }
            },

                // 
            handleAddRemove(file, fileList) {
                this.AddFormData.imgs = [];
                fileList.forEach(item => {
                    this.AddFormData.imgs.push(item.url)
                })
                console.log(this.AddFormData.imgs)
            },

            onAddSubmit() {
                this.$refs['addform'].validate((valid) => {
                    if (valid) {
                         NProgress.start();
                        lime.req("ShopWorkDayAdd", {
                            login_token: lime.cookie_get("login_token"),
                            content: this.AddFormData.content,
                            imgs: this.$refs.upload.fileListss,
                            // imgs:[this.$refs.upload.img_url],
                            // enable_check: this.AddFormData.enable_check
                            enable_check: this.enable_value
                        }).then(res => {
                            this.init();
                            this.add_show = false;
                        }).catch(err => {
                            this.$message.error(err.msg);
                        });
                        NProgress.done()
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
                console.log(this.$refs.upload.fileListss)
            },


            // 
            handleEdit() {
                if (this.curr_row == null) {
                    this.$message.error("");
                    return false;
                }
                this.enables_value = this.curr_row.enable_check
                if(this.curr_row.work_staff_name.indexOf("") != -1) {
                    lime.req('ShopWorkDayDetail', {
                    login_token: lime.cookie_get('login_token'),
                    uuid: this.curr_row.uuid
                }).then( res => {
                     let file_imgs = [];
                    if (res.data.imgs.length > 0) {
                        this.curr_row.imgs.forEach(item => {
                            file_imgs.push({
                                name:item,
                                url:item
                            })
                        })
                    }
                    this.EditFormData = {
                        uuid: res.data.uuid,
                        content:res.data.content,
                        imgs:res.data.imgs,
                        file_imgs:file_imgs,
                        enable_check:res.data.enable_check
                    }
                    this.edit_show = true;
                })
                }else {
                     this.$message.error("");
                    return false;
                }

                // let file_imgs = [];
                // if (this.curr_row.imgs.length > 0) {
                //     this.curr_row.imgs.forEach(item => {
                //         file_imgs.push({
                //             name:item,
                //             url:item
                //         })
                //     })
                // }


                // this.EditFormData = {
                //     uuid:this.curr_row.uuid,
                //     content:this.curr_row.content,
                //     imgs:this.curr_row.imgs,
                //     file_imgs:file_imgs,
                //     enable_check:this.curr_row.enable_check
                // }
                // this.edit_show = true;
            },

            onEditSubmit(){
                this.$refs['editform'].validate((valid) => {
                     NProgress.start();
                     let ay = this.$refs.uploads.img_url[0] ? [this.$refs.uploads.img_url,...this.EditFormData.imgs] : this.EditFormData.imgs
                    if (valid) {
                        lime.req("ShopWorkDayEdit", {
                            login_token: lime.cookie_get("login_token"),
                            uuid: this.EditFormData.uuid,
                            content: this.EditFormData.content,
                            imgs: ay.filter((item, index) => ay.indexOf(item) === index),

                            // imgs: [this.$refs.uploads.img_url],
                            enable_check: this.enables_value
                        }).then(res => {
                            this.init();
                            this.edit_show = false;
                        }).catch(err => {
                            this.$message.error(err.msg);
                        });
                         NProgress.done()
                    }else {
                        NProgress.done()
                        console.log(999)
                    }
                })
            },

                // 
            handleEditUploadSuccess(res, file, fileList) {
                if (res.code == 0) {
                    file.url = store.state.AppData.img_host + res.data.url;
                    
                    this.EditFormData.imgs = [];
                    fileList.forEach(item => {
                        this.EditFormData.imgs.push(item.url)
                    })
                } else {
                    this.$message.error(res.msg);
                }
            },

                // 
            handleEditRemove(file, fileList) {
                this.EditFormData.imgs = [];
                fileList.forEach(item => {
                    this.EditFormData.imgs.push(item.url)
                })
            },

            //
            handleCheck() {
                if (this.curr_row == null) {
                    this.$message.error("");
                    return false;
                }
                if(this.curr_row.work_staff_name.indexOf("") != -1) {
                    this.$message.error("");
                    return false;
                }

                lime.req('ShopWorkDayDetail', {
                    login_token: lime.cookie_get('login_token'),
                    uuid: this.curr_row.uuid
                }).then( res => {
                      this.CheckFormData = res.data;
                this.check_show = true;
                })

                // this.CheckFormData = this.curr_row;
                // this.check_show = true;
            },
            onCheckSubmit() {
                 this.$refs['checkform'].validate((valid) => {
                    if (valid) {
                        lime.req("ShopWorkDayCheck", {
                            login_token: lime.cookie_get("login_token"),
                            check_content: this.CheckFormData.check_content,
                            uuid: this.curr_row.uuid,
                            check_grade: this.CheckFormData.check_grade
                        }).then(res => {
                            this.init();
                            this.check_show = false;
                        }).catch(err => {
                            this.$message.error(err.msg);
                        });
                    }else {
                        console.log('submit error')
                    }
                 })
            },

            // 
            handleComment() {
                if (this.curr_row == null) {
                    this.$message.error("");
                    return false;
                }
                this.comment_show = true;
            },
            onCommentSubmit() {
                lime.req("ShopWorkDayComment", {
                    login_token: lime.cookie_get("login_token"),
                    content: this.CommentFormData.content,
                    shop_work_day_uuid: this.curr_row.uuid
                }).then(res => {
                    this.comment_show = false;
                }).catch(err => {
                    this.$message.error(err.msg);
                });
            },

            // 
            handleDel(menu) {
                if (util.empty(this.curr_row)) {
                    this.$message.error("");
                    return;
                }
                if(this.curr_row.work_staff_name.indexOf("") != -1) {
                    this.$confirm("?", "").then(() => {
                        lime.req("ShopWorkDayDel", {
                            login_token: lime.cookie_get("login_token"),
                            uuid: this.curr_row.uuid,
                        }).then(res => {
                            this.init();
                            this.$message.success("");
                        }).catch(err => {
                            this.$message.error(err.msg);
                        });
                    }).catch( err => {
                        console.log(err)
                    });
                }else {
                    this.$message.error("");
                }
            },

            //

            handleDetail() {
                if (this.curr_row == null) {
                    this.$message.error("");
                    return false;
                }
                lime.req('ShopWorkDayDetail', {
                    login_token: lime.cookie_get('login_token'),
                    uuid: this.curr_row.uuid
                }).then( res => {
                      this.detail_show = true;
                     this.DetailFormData = res.data
                })
                // this.detail_show = true;
                // this.DetailFormData = this.curr_row
                // this.onDetailSubmit()
            },
            onDetailSubmit() {
                lime.req("ShopWorkDayDetail", {
                    login_token: lime.cookie_get("login_token"),
                    uuid: this.curr_row.uuid
                }).then(res => {
                    this.comment_show = false;
                }).catch(err => {
                    this.$message.error(err.msg);
                });
            },
        }
    };
</script>
